import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from '../user.service';
import { HttpHeaders,HttpClient,HttpErrorResponse,HttpParams } from '@angular/common/http';
import { AbstractControl, FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { Globals } from '../globals';

@Component({
  selector: 'app-login',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})

export class LoginFormComponent implements OnInit {

  public divhide;
  private token;
  private loginForm: FormGroup;
  private username: AbstractControl;
  private password: AbstractControl;
  //private sendheaders = new HttpHeaders({'Access-Control-Allow-Origin':'*','Content-Type':  'application/json'});
  private sendheaders = new HttpHeaders({'Content-Type':'application/x-www-form-urlencoded'});
  //sendheaders.append('Access-Control-Allow-Origin':'*');
  //sendheaders.append('Accept': 'application/json');
  //sendheaders.append('Content-Type':'application/x-www-form-urlencoded');
  private loggedIn: boolean;

  constructor(private fb: FormBuilder, private httpc:HttpClient, private user:UserService, private router:Router, private globals:Globals) { 
  this.createForm();
  }

  createForm() {
    this.loginForm = this.fb.group({
      username: [ '', Validators.required ],
      password: [ '', Validators.required ]
    });
    this.username = this.loginForm.controls['username'];
    this.password = this.loginForm.controls['password'];
  }

  ngOnInit() {
  }

  loginUser(e) {
    e.preventDefault();
    console.log(e);
    console.log('teest!!');
    var username = this.username.value;
    var password = this.password.value;
    console.log(username+'-'+password);
    this.divhide=true;

    this.loginf(username,password);
  }

  loginf(username,userpassword) {
    //this.httpc.post('http://localhost/angtasks/capiangtasks/api/login?user='+username+'&password='+userpassword,JSON.stringify({user: username, password: userpassword}),{headers: this.sendheaders})
    const body = new HttpParams()
    .set('user', username)
    .set('password', userpassword);
    console.log(this.sendheaders.get('Content-Type'));
    this.httpc.post(this.globals.base_url+'/api/login',body.toString(),{headers: this.sendheaders})
        .toPromise()
         .then(res => {
          this.token=res['token'];
          //console.log(this.token);
          if(this.token) {
            this.globals.token = this.token;
            setTimeout(function(){ 
            this.user.setUserLoggedIn();
            }.bind(this), 500);
            const newheader=new HttpHeaders({
              'Access-Control-Allow-Origin':'*',
              'Authorization':'Bearer ' + this.token,
              'Content-Type':'application/x-www-form-urlencoded'
            });
            //this.sendheaders=this.sendheaders.append('Access-Control-Allow-Origin','*');
            //this.sendheaders=this.sendheaders.append('Authorization','Bearer ' + this.token);
            //newheader=newheader.append('Authorization','Bearer '+this.token);
            //newheader=newheader.append('Content-Type','application/json');
            //newheader=newheader.append('Access-Control-Allow-Origin','*');
            //console.log(newheader.get('Content-Type'));
            console.log(newheader.get('Authorization'));
            //console.log(newheader.get('Access-Control-Allow-Origin'));
            //this.httpc.get('http://localhost/angtasks/capiangtasks/api/login',{headers:new HttpHeaders({'Access-Control-Allow-Origin':'*','Content-Type':'application/json','Authorization':'Bearer '+this.token})})
            setTimeout(function(){ 
            this.httpc.get(this.globals.base_url+'/api/login',{headers: newheader})
                  .subscribe(res =>{
                  //alert(JSON.stringify(res[0]['username']));
                  //this.user.username=username;
                  this.user.username=res[0]['username'];
                  this.router.navigate(['dashboard']);
                  });
                }.bind(this), 1000);
            
          }
         },
        (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          console.log("Client-side error occured.");
        } else {
          console.log("Server-side error occured.");
          console.log(err.status);
          if(err.status){
            this.username.reset();
            this.password.reset();
          }
        }
      });
  }
}
