import { Injectable } from '@angular/core';
import { Globals } from './globals';

@Injectable()
export class UserService {

  private isUserLoggedIn;
  public username;

  constructor(private globals: Globals) { 
  	this.isUserLoggedIn = false;
  }

  setUserLoggedIn() {
  	this.isUserLoggedIn = true;
    this.username = 'admin';
    this.globals.islogin = true;

    localStorage.setItem('isUserLoggedIn', this.isUserLoggedIn);
    localStorage.setItem('token', this.globals.token);
    localStorage.setItem('username', this.username);
  }

  getUserLoggedIn() {
    this.isUserLoggedIn=localStorage.getItem('isUserLoggedIn');
  	return this.isUserLoggedIn;
  }

  UserLogout(){
    this.isUserLoggedIn = false;
    this.globals.islogin = false;
    localStorage.setItem('isUserLoggedIn', 'false');
    localStorage.setItem('username', '');
    localStorage.setItem('token', '');
  }
}
