import { Component, OnInit } from '@angular/core';
import { HttpHeaders,HttpClient,HttpErrorResponse,HttpParams } from '@angular/common/http';
import {UserService} from '../user.service';
import {Router} from '@angular/router';
import { Globals } from '../globals';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
 	
  private name = 'anonymous';
  private anounces;

  constructor(private user: UserService, private router: Router, private globals:Globals,private httpc:HttpClient) { }

  ngOnInit() {
    this.globals.showLoading=true;
  	this.name = this.user.username;
  	console.log('Is user logged in? ', this.user.getUserLoggedIn());
    const newheader=new HttpHeaders({
              'Access-Control-Allow-Origin':'*',
              'Authorization':'Bearer ' + this.globals.token,
              'Content-Type':'application/x-www-form-urlencoded'
            });

    console.log(newheader.get('Authorization'));
    setTimeout(function(){ 
    this.httpc.get(this.globals.base_url+'/api/anounces',{headers: newheader})
          .subscribe(res =>{
          //alert(JSON.stringify(res));
          this.anounces = res['response'];
          this.globals.showLoading=false;
        });
      }.bind(this), 500);
  }

  logout() {
  	this.user.UserLogout();
  	this.router.navigate(['/']);
  }
}
