import { Injectable } from "@angular/core";


@Injectable()
export class Globals {
  constructor(){
	if(localStorage.getItem('token')){
		this.token=localStorage.getItem('token');
	}
  }
  public baseang_url: string = 'http://localhost/anganunt/dist';
  public base_url: string = 'http://localhost/anganunt/capianganunt';
  public showLoading: boolean = true;
  public role: string = 'test';
  public islogin: boolean = false;
  public token: string;
  public image_upload = [];
  public colorbutton: string = 'btn btn-blue-grey waves-light';
  public colorbackground: string = 'silver';
  public colorformbg: string = 'pink';
}