import { ViewEncapsulation,Component } from '@angular/core';
import { Event, Router, NavigationStart, NavigationEnd } from '@angular/router';
import { filter, debounceTime } from 'rxjs/operators';
import { Globals } from './globals';
import {TranslateService} from '@ngx-translate/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent {
  private title = 'app';
  private showLoading = true;

  constructor(private globals:Globals, private router:Router,public translate: TranslateService) { 
  this.router.events.pipe(
      filter((event) => event instanceof NavigationEnd),
      debounceTime(1000)
    ).subscribe((routerEvent: Event) => {
    this.globals.showLoading = false;
  });
    translate.addLangs(['en', 'fr', 'ro']);
    translate.setDefaultLang('en');

    const browserLang = translate.getBrowserLang();
    translate.use( browserLang.match(/en|fr|ro/)?browserLang:'en' );
  }

  ngOnInit() {
        //$() = this.globals.colorbackground;
        $('body').css('background-color', this.globals.colorbackground);
   }

}
